<template>
    <div class="sidebarContents">
        <div class="fixedContent">
          <div class="logo" v-on:click="goHome">
            <h1>TURF</h1>
          </div>
          <router-link to="/getting-started" tag="li" class="menuItem heading">Getting Started</router-link>
          <input id="sidebarFilter" placeholder="Search modules" v-on:keyup='filterList'>
        </div>
        <ul class="turfModules">
          <li v-for="module in displayedModules"
            class="menuItem"
            v-bind:class="{heading: module.isHeading}" 
            v-on:click="clickModule"
            >{{module.name}}</li>
        </ul>
    </div>
</template>

<script>
import {Col} from 'iview/src/components/grid'

export default {
  name: 'Sidebar',
  props: ['modules'],
  components: {
    Col
  },
  computed: {
    displayedModules: function () {
      return this.modules.filter(function (module) {
        return module.hidden === false
      })
    }
  },
  methods: {
    goHome: function () {
      this.$router.push('/')
    },
    clickModule (e) {
      if (e.target.classList.value.includes('heading')) return
      this.$router.push('docs')
      window.scrollTo(0, 0)
      this.$emit('changeModule', e.target.innerText)
    },
    filterList (e) {
      this.$emit('filterChanged', e.target.value.toUpperCase())
    }
  }
}
</script>

<style lang="scss">
  @import "../styles/variables.scss";

  .sidebarContents {
    top: 0;
    bottom: 0;
    width: inherit;

    .fixedContent {
      width: inherit;
      position: fixed;
      padding-left: 30px;
        .logo {
        height: 120px;
        background-color: #2ECC71;
        margin-bottom: 30px;
        margin-left: -35px;
        margin-right: 0px;
        cursor: pointer;
          h1 {
            font-size: 2rem;
            color: white;
            text-align: center;
            letter-spacing: 0.4rem;
            padding-top: 30px;
            font-weight: 600;
          }
        }

        #sidebarFilter {
          margin: 30px 0px 30px -10px;
          font-size: 0.9rem;
          width: 80%;
          padding: 5px;
          border: solid 3px #efefef;
          color: #656565;
          transition: 0.3s;
          &:focus {
            outline: none;
            border: solid 3px #cccccc;      
          }
        }
    }

    .turfModules {
      position: fixed;
      overflow-y: scroll;
      top: 260px;
      bottom: 0;
      width: inherit;
      padding-left: 30px;
        &::-webkit-scrollbar-track{
          -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
          background-color: transparentize($blue, 0.98);
        }

        &::-webkit-scrollbar{
          width: 6px;
          background-color: transparentize($blue, 0.98);
        }

        &::-webkit-scrollbar-thumb{
          background-color: transparentize($blue, 0.7);
        }
      }

    .menuItem {
      margin: 5px 0px;
      color: #656565;
      text-decoration: none;
      cursor: pointer;
      list-style-type: none;
    }
    .heading {
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 10px;
      font-size: 14px;
      margin-left: -8px;
    }
  }

</style>



